@mixin _flatpickrMixin($colors) {
  .flatpickr-calendar {
    background: map-get($colors, "flatpicker-background");
  }

  .flatpickr-calendar.showTimeInput.hasTime .flatpickr-time {
    border-top: 1px solid map-get($colors, "flatpicker-background");
  }

  .flatpickr-calendar.arrowTop:before {
    border-bottom-color: map-get($colors, "flatpicker-background");
  }

  .flatpickr-calendar.arrowTop:after {
    border-bottom-color: map-get($colors, "flatpicker-background");
  }

  .flatpickr-calendar.arrowBottom:before {
    border-top-color: map-get($colors, "flatpicker-background");
  }

  .flatpickr-calendar.arrowBottom:after {
    border-top-color: map-get($colors, "flatpicker-background");
  }

  .flatpickr-months .flatpickr-month {
    color: map-get($colors, "flatpicker-color");
    fill: map-get($colors, "flatpicker-color");
  }

  .flatpickr-months .flatpickr-prev-month,
  .flatpickr-months .flatpickr-next-month {
    color: map-get($colors, "flatpicker-color");
    fill: map-get($colors, "flatpicker-color");
  }

  .flatpickr-months .flatpickr-prev-month:hover,
  .flatpickr-months .flatpickr-next-month:hover {
    color: map-get($colors, "flatpicker-color-hover");
  }

  .flatpickr-months .flatpickr-prev-month:hover svg,
  .flatpickr-months .flatpickr-next-month:hover svg {
    fill: map-get($colors, "flatpicker-arrow-color-hover");
  }

  .flatpickr-current-month .numInputWrapper span.arrowUp:after {
    border-bottom-color: map-get($colors, "flatpicker-arrow-color");
  }

  .flatpickr-current-month .numInputWrapper span.arrowDown:after {
    border-top-color: map-get($colors, "flatpicker-arrow-color");
  }

  .flatpickr-current-month .flatpickr-monthDropdown-months .flatpickr-monthDropdown-month {
    background-color: map-get($colors, "flatpicker-background");
  }

  span.flatpickr-weekday {
    color: map-get($colors, "flatpicker-weekday-color");
  }

  .flatpickr-day {
    color: map-get($colors, "flatpicker-color");
  }

  .flatpickr-day .prevMonthDay,
  .flatpickr-day.selected {
    color: map-get($colors, "flatpicker-active-color") !important;
  }

  .flatpickr-day.inRange,
  .flatpickr-day.prevMonthDay.inRange,
  .flatpickr-day.nextMonthDay.inRange,
  .flatpickr-day.today.inRange,
  .flatpickr-day.prevMonthDay.today.inRange,
  .flatpickr-day.nextMonthDay.today.inRange,
  .flatpickr-day:hover,
  .flatpickr-day.prevMonthDay:hover,
  .flatpickr-day.nextMonthDay:hover,
  .flatpickr-day:focus,
  .flatpickr-day.prevMonthDay:focus,
  .flatpickr-day.nextMonthDay:focus {
    background: rgba(map-get($colors, "flatpicker-color-inactive"), 50%);
    border-color: transparent;
  }

  .flatpickr-day.today {
    border-color: map-get($colors, "flatpicker-color-today");
  }

  .flatpickr-day.selected,
  .flatpickr-day.startRange,
  .flatpickr-day.endRange,
  .flatpickr-day.selected.inRange,
  .flatpickr-day.startRange.inRange,
  .flatpickr-day.endRange.inRange,
  .flatpickr-day.selected:focus,
  .flatpickr-day.startRange:focus,
  .flatpickr-day.endRange:focus,
  .flatpickr-day.selected:hover,
  .flatpickr-day.startRange:hover,
  .flatpickr-day.endRange:hover,
  .flatpickr-day.selected.prevMonthDay,
  .flatpickr-day.startRange.prevMonthDay,
  .flatpickr-day.endRange.prevMonthDay,
  .flatpickr-day.selected.nextMonthDay,
  .flatpickr-day.startRange.nextMonthDay,
  .flatpickr-day.endRange.nextMonthDay {
    color: map-get($colors, "flatpicker-inactive");
    background: map-get($colors, "flatpicker-active-background");
    border-color: map-get($colors, "flatpicker-active-background");
  }

  .flatpickr-day.flatpickr-disabled,
  .flatpickr-day.prevMonthDay,
  .flatpickr-day.nextMonthDay,
  .flatpickr-day.notAllowed,
  .flatpickr-day.notAllowed.prevMonthDay,
  .flatpickr-day.notAllowed.nextMonthDay {
    color: map-get($colors, "flatpicker-color-inactive");
    opacity: .5;
  }

  .flatpickr-day.flatpickr-disabled:hover,
  .flatpickr-day.prevMonthDay:hover,
  .flatpickr-day.nextMonthDay:hover,
  .flatpickr-day.notAllowed:hover,
  .flatpickr-day.notAllowed.prevMonthDay:hover,
  .flatpickr-day.notAllowed.nextMonthDay:hover {
    color: map-get($colors, "flatpicker-color-inactive-hover");
    background: map-get($colors, "flatpicker-color-inactive-background-hover");
  }

  .flatpickr-calendar [disabled],
  .flatpickr-calendar [disabled]:hover {
    color: map-get($colors, "flatpicker-muted-color") !important;
    opacity: .7;
  }

}